.divMenuBar {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    /* justify-content: space-around; */
    margin-left: 30%;
    padding-left: 10%;
    overflow: auto;
    justify-content: flex-start;
    align-items: center;

    white-space: nowrap; /* اضافه شده */
    height: 3.5rem;
    min-width: 100%; /* اضافه شده */
  }
  
  .menu > button {
    display: inline-block;
    text-align: center;
    align-items: center;
    border-radius: 10px;
    background-color: #3B3B3B;
    border: none;
    outline: none;
    cursor: pointer;
    width: auto;
    color: white;
    font-size: 1rem;
    min-width: 3rem;
    /* max-width: 10rem; */
  }
  .menu-container {
  
    width: 80%;
    padding-left: 35%;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    overflow-x: auto;
    white-space: nowrap;
    height: 50px;
    cursor: pointer;
    scrollbar-width: thin;
    scrollbar-color: #A259FF #2b2b2b;
    /* margin-left: 8%;
    padding-left: 7%; */
  }
  
  .menu {
    display: flex;
  gap: 1rem;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .z {
    color: white;
  }
  
  .z.active {
    color: #A259FF;
  }
  
  .menu a {
    display: block;
    padding: 0 10px;
    line-height: 35px;
    text-decoration: none;
    color: white;
    font-size: 16px;
    background-color: #3B3B3B;
    border-radius: 8px;
  }
  
  .menu a:hover {
    color: #A259FF;
    opacity: 0.85;
  }

  @media    (max-width: 800px){
    .menu-container{
      padding: 0;
    } 
  }
  @media    (800px <= width <= 1300px){
    .menu-container{
      padding-left: 6%;
    }
    
  }