.div_watch_down{
    display: flex;
    flex-direction: column;
    margin-left: 16%;
    margin-top: 0.5rem;
    position: relative;
    width: 100%;
    max-width: 1120px;
    height: auto;
}
.div_top{
    background-color: rgb(13, 12, 12);

    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    
}
.div_top_right{
    display: flex;
    padding-left: 0.5rem;
    flex-direction: column;
}
.changer{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}
.changer > p{
    font-size: 0.7rem;
    font-weight: bold;
}

.logosss{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
}
.btn_sub_watch{
    border-radius: 20px;
    background-color: #A259FF;
    color: white;
    cursor: pointer;
    text-align: center;
    outline: none;
    border: none;
    width: 8rem;
    height: 3rem;
    font-size: 1.15rem;

}
.btn_sub_watch:hover{
    opacity: 0.7;


}
.left_watch{
    display: flex;
    flex-direction: row;
    align-items: end;
    padding-bottom: 0.5rem;
    gap: 1rem;
    padding-right: 0.5rem;
}
.like_div_watch{
    display: flex;
    align-items: center;
    flex-direction: row;
    background-color: #2b2b2b;
    border-radius: 15px;
    width: 8rem;
    height: 2.2rem;
    gap: 0.5rem;
    justify-content: center;
}
#like{
    cursor: pointer;
}
#like:hover{
    opacity: 0.5;
    
    
}
#p_sep{
    font-size: 2rem;
    font-weight: bold;
}
.div_share{
    display: flex;
    align-items: center;
    flex-direction: row;
    background-color: #2b2b2b;
    border-radius: 15px;
    width: 6rem;
    height: 2.2rem;
    gap: 0.5rem;
    justify-content: center;
    cursor: pointer;
}
.footer{
    display: flex;
    flex-direction: column;
    background-color: black;
    margin-top: 1rem;
    border-radius: 15px;
    align-items: start;
    padding-left: 0.5rem;
    width: 100%;
    height: auto;
    
}
@media    (400px <= width <= 1200px){
    
    
     .div_watch_down{
       width:78vw;
       margin-left: 5%;
       height: auto;
 
     }
     .left_watch{
        display: flex;
        flex-direction: column;
        height: auto;
     }
     .like_div_watch{
         width: auto;
          margin-top: 1rem;  
     }
   }
