.div_video_card{
    display: grid;
    margin-left: 7%;
    gap: 1rem;
    padding-left:5%;
    padding-top: 1rem;
    grid-template-columns: auto auto auto auto;
}
.card{
    display: flex;
    flex-direction: column;
    white-space: nowrap; /* جلوگیری از شکستن خطوط */
    overflow: hidden; /* پنهان کردن متن اضافی */
    text-overflow: ellipsis; /* نماد سه نقطه برای کوتاه کردن متن */
    font-size: 0.7rem;
    width: 95%;
    height: auto;
    border-radius: 10px;
}
.top_card{
    cursor: pointer;
    border-radius: 10px;
   height: 66%;
   width: 100%;


}
.top_card:hover{
    opacity: 0.65;
}
.mid_card{
    display: flex;
    align-items: center;
    flex-direction: row;
    gap:0.75rem;
    height: 20%;

    font-weight: bold;
}
.card_logo{
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
}
.down_card{
    padding-left: 3rem;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-self: start;
    flex-direction: column;
    font-size: 0.85rem;
}
.down_card > p  , .down_card >p >span{
    color: darkgray;
}
.down_card >p:nth-child(2){
    margin-top: -5px;
}
@media    (width< 500px){
    .div_video_card{
        display: grid;
        grid-template-columns: auto;
        margin-top: 1rem;
        padding: 0;
    }
    .card{
        width: auto;
    }
  }
  @media    (500px <= width <= 1300px){
    .div_video_card{
        display: grid;
        grid-template-columns: auto auto;
    }
   
    
  }

