
.i{
    color: #A259FF;
}
.main{
    align-items: center;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    display: grid;
    grid-template-columns: auto auto;
    width: 100%;
    height: 100%;
}
.left_side{
    /* background-color: beige; */
    width: auto;
    justify-content: space-around;
    align-items: flex-start;
    height: 96vh;
    display: flex;
    flex-direction: column;
    padding-left: 10vw;
    padding-top: 4vh;
}
.up_left_side{
    /* background-color: aqua; */
    display: flex;
    flex-direction: column;
    width: auto;
    
}
.down_left_side{
    padding-bottom: 32vh;
    /* background-color: blue; */
    display: grid;
    row-gap: 1.5vw;
    /* margin-top: -2vh; */
    grid-template-columns: auto auto;
    width: auto;
    height: 30vh;
}

.right_side{
   
    
    /* background-color: red; */
    width: 50vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
}
.logo1{
    width: 5rem;
    height: 5rem;
    border-radius: 10px;
}
.h1{
    font-family: SpaceMono;
    font-size: 3.5vw;
    margin-top: 2vh;
}
.p1{
  font-family: WorkSans;
  font-size: 1.5vw;
  margin-left: 1vw;

}

button{
    width: 10vw;
    height: 4vh;
    font-family: WorkSans;
    text-align: center;
    
    color: #A259FF;
    border: 1px solid #A259FF;
    border-radius: 15px;
    cursor: pointer;
  }
  button:hover{
    background-color: #A259FF;
    border: 1px solid black;
    color: white;
  }
  i{
    color: #A259FF;
  }
  .imgright{
    width: 50vw;
    height: 99vh;
    opacity: 0.3;

  }
 
  
@media only screen and (max-width: 800px) {
   
    .logo{
        width: 2.5rem;
        height: 2.5rem;
        
    }
    .imgright{
        width: 50vw;
        height: 75vh;
    }
    h1{
        font-size: 19.6px;
    }
    p{
        font-size: 15.6px;
    }
    
   
    button{
        width:4rem ;
        height: 2rem;
    }
   
  }




  