.div_watch_side{
    display: flex;
    flex-direction: column;
    width: auto;
    gap: 1rem;
    margin-left: 11%;
    margin-top: 2rem;
}
.watch_card{
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
    width: 22rem;
    height: 7rem;
}
.img_watch_side{
    width: 45%;
    border-radius: 10px;
    height: 100%;
    cursor: pointer;
}
.watch_card_right{
    display: grid;
    grid-row: auto auto auto;
    align-items: start;
    width: 55%;
    white-space: nowrap; /* جلوگیری از شکستن خطوط */
    overflow: hidden; /* پنهان کردن متن اضافی */
    text-overflow: ellipsis; /* نماد سه نقطه برای کوتاه کردن متن */    
}
.watch_card_right > p,.div_last >p{
    white-space: nowrap; /* جلوگیری از شکستن خطوط */
    overflow: hidden; /* پنهان کردن متن اضافی */
    text-overflow: ellipsis; /* نماد سه نقطه برای کوتاه کردن متن */
    font-size: 0.7rem;
}

.div_last{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}
@media    (500px <= width <= 1200px){
    .div_watch_side{
        display: none;
    }
    
  }