

.main1{
    width: 100%;
    height: 42rem;
    display: flex;
    gap: 3rem;
    flex-direction: row;


}
ul{
    list-style: none;
}
.ul_right{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 50%;
    padding-top: 8rem;
}
.first_li ,.secound_li{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}
.secound_li > p{
    font-size: 1.3rem;
}
h3{
    font-size: 3rem;
}
.ul_left{
    margin-top: 8%;
    left: 0;
    right: 0;
    width: 50%;
    height: 100%;
    margin-left: 2rem;
}
.img_left{
    width: 100%;
    height: 100%;
}

.last_li{
    display: grid;
    flex-direction: column;
    gap: 1rem;
}
.last_li > div{
    width: 20rem;
    background-color: darkgray;
    height: 3rem;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 1rem;
    align-items: center;

}
.last_li > div:nth-child(5){
    width: 20rem;
    flex-direction: row;
    background-color: #2b2b2b;
    height: 3rem;
    padding-left: 1rem;
    margin-top: 0.5rem;
    font-weight: bold;
    align-items: center;
    
    

}
.last_li > div:nth-child(5) > a{
    color: #A259FF;
    outline: none;
}
.last_li > button{
    width: 20rem;
    background-color: #A259FF;
    height: 3rem;
    border-radius: 20px;
    border: 0px;
    color: white;
    text-align: center;
    align-items: center;
    font-size: 1.1rem;
    cursor: pointer;
}
.divpassActiveF{
    border: 3px solid;
    border-color: #ff3333;
}

.divpassActiveS{
    border: 3px solid;
    border-color: #4BB543;
}

.p_error{
    color: #ff3333;
}
.username{
    border: 0px;
    border-radius: 20px;
    height: 100%;
    width: 90%;
    outline: none;
    color: #2b2b2b;
    background: none;
    
    font-size: 1.2rem;
}
.last_li> div>i{
    color: rgb(115, 115, 115);
}

@media only screen and (max-width: 800px) {
    main{
        display: flex;
        flex-direction: column;
        height: auto;
        gap: 0rem;
    }
    .ul_right{
        width: 100%;
        align-items: center;
    }
    .ul_left{
        width: 100%;
        height: 14rem;
    }

}