.div_navbar{
    display: flex;
    top: 0;
    width: 100%;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    height: 10%;
}
.navbar_left{
    display: grid;
    width: 10%;
    align-items: center;
    grid-template-columns:40% 45% 15%;
    gap: 1rem;
}


.logo{
    border-radius: 50%;
    width: 30px;
    cursor: pointer;
    height: 30px;
    margin-left: 1rem;
}
.logo:hover{
    width: 40px;
    height: 40px;
}
.navbar_search{
    width: 33%;
    height: 2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 15px;
    border: 0.1px solid darkgray;

    
}

input{
    padding-left: 1rem;
    width: 90%;
    height: 92%;
    outline: none;
    border-radius: 15px;
    border: none;
    background-color: #2b2b2b;

}

.btn_search{
    background-color: #3B3B3B;
    width: 10%;
    cursor: pointer;
    height: 99%;
    border-top-right-radius:15px;
    border-bottom-right-radius:15px ;
    border: none;
    

}
.navbar_signin{
    display: flex;
    flex-direction: row;
    width: auto;
    justify-content: center;
    align-items: center;
    
    
}
.btn_sign_in{
    height: 40px;
    width: 100%;
    border-radius: 15px;
    border: 0.1px solid darkgray;
    display: flex;
    flex-direction: row;
    background-color: none;
    gap: 1rem;
    align-items: center;
    background-color: #2b2b2b;
    cursor: pointer;
}

.btn_sign_in > p{
    color: white;
}
/* .search_mobile {
    display: none;
} */
.div_mobile_search{

    display: none;
}
.i_s{
    margin-right: 1rem;
    cursor: pointer;
}
.i_s:hover{
    opacity: 0.5;
}
@media   (max-width: 800px){
    .search_mobile{
        background-color: #2b2b2b;
        outline: none;
        border: none;
        cursor: pointer;
    
    }
    .div_mobile_search{
        width: 100%;
        height: auto;
        align-items: center;
        display: grid;
        grid-template-columns: 20% 60% 20%;
        justify-content: space-between;
    }
    .middle_box{
        width: 100%;
        height: 2rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        border-radius: 15px;
        border: 0.1px solid darkgray;
    }
    .logo_mobile{
        border-radius: 50%;
        width: 30px;
        cursor: pointer;
        height: 30px;
        margin-left: 1rem;
    }
    .btn_back{
        background-color: #2b2b2b;
        outline: none;
        border: none;
        cursor: pointer;
    }
    .navbar_search{
        display: none;
    }
   
    .logo{
        display: none;
    }
  }