@font-face {
    font-family: SpaceMono;
    src: url("/public/font/SpaceMono-Regular.ttf");
    
}
@font-face {
    font-family: WorkSans;
    src: url("/public/font/WorkSans-Regular.ttf");
  
}
*{
    color: white;
    font-family: WorkSans;
   
}
body{
    background-color: #2b2b2b;
}
.div_sidebar{
    display: flex;
    position: fixed;
    flex-direction: column;
    gap: 1rem;
    overflow-y: auto;
    left: 0;
    width: auto;
    height: 100%;
    scrollbar-color: #A259FF #2b2b2b;

    scrollbar-width: thin; /* عرض اسکرول بار */
}
.div_sidebar > p{
    padding-left: 4%;
    font-size: 25;
}
.div_inside{
    display: flex;
    padding-left: 4%;
    flex-direction: row;
    gap: 1rem;
    border-radius: 15px;
    align-items: center;
    cursor: pointer;
    height: 2.2rem;
}
.div_inside:active{
    background-color: #3B3B3B;
}
.div_inside >p{
    font-size: 20;
}
.div_inside:hover{
    background-color: #3B3B3B;
}
.divider {
    height: 0.5px;
    width: 100%;
    background-color: darkgray;
  }
  .copyright{
    top: 0;
    left: 0;
    right: 0;
    display: block;
    margin: auto;
    padding-left: 4%;
  }
  .copyright > h6{
    font-size: 10;
    align-items: center;
  }
  .copyright > p{
    font-size: small;
    color: darkgray;
  }
#p_all{
    display: block;
}


  @media    (max-width: 1300px){
    /* .div_sidebar{
        display: none;
    } */
    .div_sidebar{
        background-color: #2b2b2b;
    }
  }